import React, {useState, useEffect} from 'react';
import styles from './BackgroundVideo.module.scss';

function App() {
  const [loop, setLoop] = useState(false);
  const [loadState, setLoadState] = useState('wait');
  /** ÂãïÁîª„ÅåË™≠„ÅøËæº„ÅøÁµÇ„Çè„Å£„Å¶Ëá™ÂãïÂÜçÁîü„ÅåÂßã„Åæ„Å£„ÅüÊôÇ„Å´„Éï„Çß„Éº„Éâ„Ç§„É≥„Åï„Åõ„Çã„Åü„ÇÅ„ÅÆ„Éè„É≥„Éâ„É© */
  const onPlay = function () {
    setLoadState('loaded');
  };

  // „É¶„Éº„Ç∂„ÅåÂãï„Åç„ÅÆÂäπÊûú„ÇíÊ∏õ„Çâ„ÅôË®≠ÂÆö„Å´„Åó„Å¶„ÅÑ„ÇãÂ†¥Âêà„ÅØ„ÄÅÂãïÁîª„Çí„É´„Éº„ÉóÂÜçÁîü„Åï„Åõ„Å™„ÅÑ
  useEffect(() => {
    const isNoReduce = !(window.matchMedia('(prefers-reduced-motion: reduce)').matches);

    setLoop(isNoReduce);
  }, []);

  return (
    <video className={styles.video} data-state={loadState} onPlay={onPlay} tabIndex={-1} autoPlay loop={loop} muted playsInline>
      <source src="./video/nc176637-1280.webm" type='video/webm' />
      <source src="./video/nc176637-1280.mp4" type='video/mp4' />
    </video>
  );
}

export default App;
