import React, {useState, useEffect} from 'react';
import styles from './LastDate.module.scss';

/**
 * * 4„ÅßÂâ≤„ÇäÂàá„Çå„ÇãÂπ¥„ÅØ„ÅÜ„Çã„ÅÜÂπ¥
 * * 100„ÅßÂâ≤„ÇäÂàá„Çå„Å¶„ÄÅ400„ÅßÂâ≤„ÇäÂàá„Çå„Å™„ÅÑÂπ¥„ÅØ„ÅÜ„Çã„ÅÜÂπ¥„Åß„ÅØ„Å™„ÅÑ
 * @param num - Âπ¥Êï∞
 * @returns - „ÅÜ„Çã„ÅÜÂπ¥„Åã„Å©„ÅÜ„Åã
 */
const isLeapYeah = (num: number) => (
  num % 4 === 0 &&
  !(
    num % 100 === 0 &&
    num % 400 !== 0
  )
);

function LastDate() {
  const [label, setLabel] = useState('Á¥ÑÊùü„ÅÆÊó•„Åæ„Åß„ÅÇ„Å®„ÄÅ');
  const [y, setYear] = useState('');
  const [d, setDay] = useState('');
  const [h, setHour] = useState('');
  const [m, setMin] = useState('');
  const [s, setSec] = useState('');
  /** ‰ºöÈï∑„ÅÆ„ÅäÊàª„Çä 2021Âπ¥7Êúà1Êó•17ÊôÇ59ÂàÜ„Å´Booth„ÅåÁµÇ‰∫Ü„Åó„Åü„Åì„Å®„Åã„Çâ„ÄÇ */
  const end = new Date(2521, 6, 1, 18, 0, 0, 0);
  const endYear = end.getFullYear();
  const endTimestamp = end.getTime();
  /** „Ç´„Ç¶„É≥„Éà„ÉÄ„Ç¶„É≥ */
  const loop = () => {
    const now = new Date();
    const nowYeah = now.getFullYear();
    let diff = endTimestamp - now.getTime();

    {
      let leapDay = 0;
      const max = end.getFullYear() - nowYeah;

      for (let i = 0; i < max; i++) {
        if (isLeapYeah(endYear - i)) {
          leapDay++;
        }
      }

      diff -= leapDay * 24 * 60 * 60 * 1000;
    }

    // 500Âπ¥Áµå„Å£„Å¶„Å™„ÅÑ
    if (0 <= diff) {
      const year = Math.floor(diff / ( 1000 * 60 * 60 * 24 * 365 ));
      diff = diff % ( 1000 * 60 * 60 * 24 * 365 );
      const day = Math.floor(diff / ( 1000 * 60 * 60 * 24 ));
      diff = diff % ( 1000 * 60 * 60 * 24 );
      const hour = Math.floor(diff / ( 1000 * 60 * 60 ));
      diff = diff % ( 1000 * 60 * 60 );
      const min = Math.floor(diff / ( 1000 * 60 ))
      diff = diff % ( 1000 * 60 );
      const sec = String(Math.floor(diff / 1000)).padStart(2, '0');

      setYear(year ? `${year}Âπ¥` : '')
      setDay(day ? `${day}Êó•` : '')
      setHour(hour ? `${String(hour).padStart(2, '0')}ÊôÇÈñì` : '')
      setMin(min ? `${String(min).padStart(2, '0')}ÂàÜ` : '')
      setSec(`${sec}Áßí„ÄÇ`)

      return;
    }

    // 500Âπ¥Âæå
    setYear('');
    setDay('');
    setHour('');
    setMin('');
    setSec('');
    setLabel('Á¥ÑÊùü„ÅÆÊôÇ„ÅØÊù•„Åü„ÄÇ')
    clearInterval(key);
  };
  const key = setInterval(loop, 1000);

  useEffect(loop);

  // Ëá™ÂãïÁøªË®≥„É¶„Éº„Ç∂„ÅØÊõ¥Êñ∞„Åï„Çå„ÇãÊñáÂ≠óÂàó„Åî„Å®„Å´Element Node„ÅåÂà•„Çå„Å¶„ÅÑ„Çã„Åª„ÅÜ„ÅåÂä©„Åã„Çã„ÅÆ„Åß
  // „Åù„Çå„Åû„Çå„ÇíspanË¶ÅÁ¥†„ÅßÂàÜÂâ≤„Åó„Åæ„Åô
  return (
    <p>
      <span>{label}</span>
      <span className={styles.lastDate}>
        <span>{y}</span>
        <span>{d}</span>
        <span>{h}</span>
        <span>{m}</span>
        <span>{s}</span>
      </span>
    </p>
  );
}

export default LastDate;
